@import './styles/defined';

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen', 'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: $bg_default;
  color: $color_defalut;
}

.btn {
  overflow: hidden;
  position: relative;
  &::before {
    content: '';
    position: absolute;
    top: -50%;
    right: -50%;
    bottom: -50%;
    left: -50%;
    margin: auto;
    transform: scale(0.5);
    border: 1px solid #ccc;
    border-radius: 5px;
    pointer-events: none;
    z-index: 1;
  }
}

.float-btn {
  position: fixed;
  bottom: 60px;
  right: 30px;
  background: rgba($color: #ffffff, $alpha: 0.9);
  z-index: 1000;
  height: 66px;
  width: 66px;
  border-radius: 66px;
  box-shadow: 0 0 5px 1px rgba($color: #666666, $alpha: 0.1);
  display: flex;
  align-items: center;
  justify-content: center;
  &:active {
    background: rgba($color: #eeeeee, $alpha: 0.9);
  }
}
