@import '../../styles/defined';

.daily {
  padding-bottom: 50px;

  &-list {
    &-header {
      position: sticky;
      top: 0;
      font-size: 17px;
      padding: 15px;
      background: $bg_default;
      box-shadow: 0 2px 3px 0px rgba(0, 0, 0, 0.05);
    }
  }

  &-add {
    bottom: 120px;
    transform: rotate(45deg);
  }

  &-more {
    bottom: 40px;
  }

  &-modal-input-box {
    margin: 10px 15px;
  }
}
