.index {
  padding-bottom: 50px;

  &-list {
    &-header {
      font-size: 17px;
      color: #333;
      padding: 2px 0 4px;
    }
    &-item-title {
      padding: 5px 0;
    }
    .am-list-item {
      &:active {
        background-color: rgba($color: #666666, $alpha: 0.1);
      }
    }
  }

  &-add {
    bottom: 120px;
    transform: rotate(45deg);
  }

  &-more {
    bottom: 40px;
  }

  &-modal-input-box {
    margin: 10px 15px;
  }
}
